.phone-menu {
  z-index: 9;
  background-color: #2c2c2c;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: all .3s;
  animation-name: menu-appears;
  animation-duration: .3s;
  animation-fill-mode: forwards;
  display: none;
  position: fixed;
  top: 0;
  left: -100vw;
}

.phone-menu--visible {
  display: flex;
}

.phone-menu_list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.phone-menu_list_link {
  color: #fff;
  width: max-content;
  position: relative;
}

.phone-menu_list_link:after {
  content: "";
  background-color: #2060f6;
  width: 0;
  height: 2px;
  transition: width .3s ease-in-out;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.phone-menu_list_link:hover:after {
  width: 100%;
}

.close-menu {
  z-index: 1000;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-menu_stick {
  background-color: #fff;
  width: 24px;
  height: 2px;
  margin: 3px 0;
  transition: transform .3s;
}

.close-menu_stick:first-child {
  position: absolute;
  transform: rotate(45deg);
}

.close-menu_stick:last-child {
  position: absolute;
  transform: rotate(-45deg);
}

@keyframes menu-appears {
  from {
    opacity: .1;
    left: -100vw;
  }

  to {
    opacity: 1;
    left: 0;
  }
}

